import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';

const app = getApp().globalData;
const {
	navBar,
	appid,
	appsecret,
	isWeixin
} = app;
export default {
	data() {
		return {
			display: true,
			navBar: navBar,
			showWechat: false,
			logo: '',
			mobile: '',
			password: '',
			scrollHeight: '',
			newHeight: '',
			count: 0
		};
	},
	methods: {
		showTabbar() {
			this.tabbar = true;
		},
		hideTabbar() {
			this.tabbar = false;
		},

		clickLeft() {
			uni.hideKeyboard();
			setTimeout(() => {
				uni.switchTab({
					url: '/pages/tabar/index'
				});
			}, 300)

		},
		pageUrl(data) {
			uni.hideKeyboard()
			setTimeout(() => {
				uni.navigateTo({
					url: data
				})
			}, 300)

		},
		// 手机登录
		mobileLogin() {
			let {
				mobile,
				password
			} = this;
			let timeStamp = Math.round(new Date().getTime() / 1000);
			if (!mobile) {
				rs.Toast('手机号码不能为空，请输入手机号');
				return;
			}
			if (!password) {
				rs.Toast('密码不能为空，请输入密码');
				return;
			}
			if (password.length < 6) {
				rs.Toast('密码不能少于六位');
				return;
			}
			this.count++;
			if(this.count!=1)return;
			setTimeout(()=>{this.count=0},1000)
			let obj = {
				mobile: mobile,
				password: password,
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);

			rs.postRequests('login', params, res => {
				let data = res.data;
				if (data.code == 200) {
					rs.Toast('登录成功，将跳转到首页');
					uni.setStorageSync('cdj_token', data.data.token);
					uni.setStorageSync('is_child', data.data.is_child);
					// console.log(data)













					uni.setStorageSync('is_miniBind', data.data.alipayBind);

					setTimeout(() => {
						uni.switchTab({
							url: '/pages/tabar/index'
						});
					}, 1000)

				} else {
					rs.Toast(data.msg);
				}
			});
		},

		// app端微信登录


























































		// H5端微信登录











































		// 微信端微信登录



























































		// 支付宝

		wechatLogin() {
			this.count++;
			if(this.count!=1)return;
			setTimeout(()=>{this.count=0},1000)
			uni.login({
				provider: 'alipay',
				scopes: 'auth_user',
				success(res) {
					console.log(res)
					uni.getUserInfo({
						success(infos) {
							console.log(infos);
							let timeStamp = Math.round(new Date().getTime() / 1000);
							let obj = {
								appid: appid,
								timeStamp: timeStamp,
								code: res.code
							};
							let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
							let params = Object.assign({
								type: 'mini',
								sign: sign,
								code: res.code,
								loginType: 'alipay',
								headimgurl: infos.avatar
								
							}, obj);
							rs.postRequests('wxLogin', params, (result) => {
								let data = result.data;
								if (data.code == 200) {
									rs.Toast('登录成功，将跳转到首页');
									wx.setStorageSync("cdj_token", data.data.token);
									wx.setStorageSync("is_child", data.data.is_child);
									wx.setStorageSync("is_miniBind", data.data.is_alipayBind);
									setTimeout(function() {
										uni.switchTab({
											url: '../tabar/index'
										})
									}, 1000);
								} else if (data.code == 201) {
									wx.navigateTo({
										url: 'selectway?identifying=' + data.data.identifying
									})
								} else {
									rs.Toast(data.msg);
								}
							})

						}
					})
				}
			})

		}


	},
	onShow() {
		this.display = true;




















		let timeStamp = Math.round(new Date().getTime() / 1000);
		let obj = {
			appid: appid,
			timeStamp: timeStamp
		};
		let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		let params = Object.assign({
				sign: sign
			},
			obj
		);
		rs.getRequests('indexAd', params, res => {
			let data = res.data;
			if (data.code == 200) {
				this.logo = data.data.logo;
			}
		});

		//H5微信登录








































	}
};
